/* COVER */

.cover-box {
    width: 100%;
}
.cover {
    max-width: 100%;
    height: auto;
    user-select: none;
    object-fit: contain;
    box-shadow: 0px 0px 20px 1px rgb(0 0 0 / 10%);

    /* NEW  */
    border-radius: 20px;
}

/* TRACK DETAILS */

.prod {
    color: var(--prodColor);
}

/* CONTAINER SIZING */
.container {
    width: 100% !important;
    max-width: 604px !important;
}
@media (max-width: 1200px) {
    .container {
        max-width: 604px !important;
    }
}


/* FOOTER */

