/* LISTEN BOX */

.listen-box button {
    transition: all 0.3s ease;
}
/* NEW */
@media (max-width: 563px) {
    .listen-box button {
        justify-content: flex-start !important;
        width: 100% !important;
    }
}

/* UNUSED NOW */
.listen-box img {
    cursor: pointer;
    border: 1px solid var(--helpColor);
    border-radius: 10px;
    padding: 3px 6px;
}
.listen-box img:hover {
    border-color: var(--colorHover);
}


/* CUSTOM HOVER LINKS */


.spotify_link:hover {
    background-color: #1ed760 !important; /* #16ad4c */
}
.vk_link:hover {
    background-color: #0077ff !important;
}
.apple_link:hover {
    background-color: #d60017 !important;
}
.yandex_link:hover {
    background-color: #ffdb4d !important;
}
.youtube_link:hover {
    background-color: #ff0000 !important;
}

/* ALBUM */


.MuiList-root {
    background-color: var(--bgList) !important;
    color: var(--color) !important;
    border-radius: 20px !important;
}
.MuiListItem-root:hover {
    cursor: pointer;
    background-color: var(--bgListItemHover) !important;
}
.MuiTouchRipple-root {
    width: 0px !important;
}

.buttonGoToAlbum {
    font-size: 18px !important;
}
.buttonGoToAlbum svg {
    width: 20px !important;
    height: 20px !important;
}

/* chips */

.chipNew {
    border: 1px solid #d32f2fb3 !important;
}
.chipType {
    border: 1px solid #ccccccb3 !important;
}

.paper {
    background-color: var(--bgList);
    border-radius: 0px 0px 10px 10px;
    padding: 10px 20px;
}

.DrawerIsland {
    background-color: var(--bgIsland) !important;
    padding: 20px 40px;
}
.cover_f {
    width: 406px !important;
    max-width: 50% !important;
}
.islandhrefs_f {
    display: none;
}